@use "../base/variables" as v;
@use "../base/mixins" as m;

.privacy-container {
    display: flex !important;
    flex-direction: column !important;
    align-items: start !important;
    justify-content: flex-start !important;
    position: relative !important;
    height: 100% !important;
    max-width: 900px;
    margin: 0 auto 2rem auto !important;
    padding: 2rem;

    .nav-logos {
        position: relative;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        gap: 1rem;
        width: 100%;

        @include m.telefono {
            // flex-direction: row;
            gap: 2rem;
        }

        .back-button {
            position: fixed;
            top: 4rem;
            left: 3rem;
            display: inline-flex;
            align-items: center;
            gap: 0.5rem;
            margin-bottom: 2rem;
            padding: .7rem 1.5rem;
            background: v.$neutral_11;
            color: v.$main_1;
            border-radius: 1rem;
            text-decoration: none;
            text-transform: uppercase;
            font-size: 1.3rem;
            font-weight: 500;
            transition: all .3s ease;
        
            &:hover {
                color: v.$neutral_11;
                background: v.$main_2;
                border-radius: 1rem !important;
            }
        }

        .logo {
            position: relative;
            width: fit-content !important;
            margin: 0;
            font-family: v.$fuente_cursiva;
            font-weight: 900;
            font-size: 3rem;
            color: v.$neutral_12;
            text-transform: lowercase;

            @include m.telefono {
                font-size: 3.5rem;
            }

            span {
                position: absolute;
                bottom: -.2rem;
                left: 0;
                right: 0;
                margin: 0 auto !important;
                font-family: v.$fuente_principal;
                font-weight: 400;
                font-size: 1.2rem;
                text-align: center;
            }
        }

        .logo-termly {
            margin: 0 !important;
        }
    }

    .contenido-privacy {
        height: max-content !important;
        padding-bottom: 10rem !important;
    }
}

[data-custom-class='body'], [data-custom-class='body'] * {
    background: transparent !important;
}

[data-custom-class='title'], [data-custom-class='title'] * {
    /* font-family: Arial !important; */
    font-family: v.$fuente_principal !important;
    font-size: 22px !important;
    font-weight: 700;
    color: v.$neutral_11 !important;
}

[data-custom-class='subtitle'], [data-custom-class='subtitle'] * {
     /* font-family: Arial !important; */
    color: v.$neutral_09 !important;
    font-size: 14px !important;
}

[data-custom-class='heading_1'], [data-custom-class='heading_1'] * {
    /* font-family: Arial !important; */
    font-size: 19px !important;
    color: v.$neutral_11 !important;
}

[data-custom-class='heading_2'], [data-custom-class='heading_2'] * {
    /* font-family: Arial !important; */
    font-size: 17px !important;
    color: v.$neutral_11 !important;
}

[data-custom-class='body_text'], [data-custom-class='body_text'] * {
    color: #595959 !important;
    font-size: 14px !important;
    /* font-family: Arial !important; */
}

[data-custom-class='link'], [data-custom-class='link'] * {
    color: v.$main_4 !important;
    background: v.$main_2 !important;
    padding: 0.3rem !important;
    border-radius: 1rem;
    font-size: 14px !important;
    /* font-family: Arial !important; */
    word-break: break-word !important;
}